.users {
    width: 200px;
    background: white;
    overflow-y: scroll;
    max-height: calc(100vh - 150px);
    &::-webkit-scrollbar {
        width: 7px;
        background-color: white;
    }
    &::-webkit-scrollbar-thumb {
        background-color: gray;
    }
    &__item {
        padding-left: 8px;
        display: flex;
        align-items: center;
        height: 60px;
        &:hover {
            background: #e5ddd5;
            transition: 0.3s ease-in-out;
        }
    }
    &__img {
        width: 40px;
        height: 40px;
        border-radius: 100%;
        background: gray;
    }
    &__descr {
        padding-left: 8px;
    }
    &__online {
        width: 8px;
        height: 8px;
        border-radius: 100%;
        background-color: green;
        display: inline-block;
        position: relative;
        top: -1px;
    }
    &__offline {
        background-color: rgb(173, 0, 0);
    }
}
