.dialogs {
    width: 100%;
    background: #e5ddd5;
    padding: 20px;
    overflow-y: scroll;
    max-height: calc(100vh - 150px);
    &::-webkit-scrollbar {
        width: 7px;
        background-color: #e5ddd5;
    }
    &::-webkit-scrollbar-thumb {
        background-color: gray;
    }
    &__text {
        line-height: 1.5;
        font-size: 18px;
        text-align: justify;
    }
    &__item {
        border: 2px solid #dedede;
        background-color: #f1f1f1;
        border-radius: 5px;
        padding: 10px 15px;
        margin: 10px 0;
        width: 85%;
        &--dark {
                border-color: #ccc;
                background-color: #ddd;
                transform: translateX(15%);
        }
    }
    
}
