.footer {
    height: 100px;
    width: 100%;
    background: #ededed;
    display: flex;
    align-items: center;
    justify-content: center;
    &__chat {
        width: 100%;
        border: none;
        padding: 10px 20px;
        margin-bottom: 10px;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__textarea {
        resize: none;
        height: 90%;
        width: 30%;
        outline: none;
        &::-webkit-scrollbar {
            width: 7px;
            background-color: white;
        }
        &::-webkit-scrollbar-thumb {
            background-color: gray;
        }
    }
    &__btn {
        background-color: #ddd;
        border: 1px solid gray;
        margin: 5px;
        padding: 5px;
        &:hover {
            transition: 0.3s ease-in-out;
            opacity: 70%;
        }
    }
}
